<template>
 <!-- Slider main container -->
	<div ref="swiper" class="swiper" style="max-width: 816px;">
		<!-- Additional required wrapper -->
		<div class="swiper-wrapper">
			<!-- Slides -->
			<div class="swiper-slide" v-for="data in news_json.items" :key="data.title" :style="{ 'border-bottom-color':getRandomColor()}">
			<div class="slide-bg" :style="{ 'background-color': slider_bg_color}"> </div>
			<div>{{data.title}}</div>
			<div v-html="data.content"></div>
			</div>
		</div>
		<!-- If we need pagination 
		<div class="swiper-pagination"></div>-->

		<!-- If we need navigation buttons 
		<div class="swiper-button-prev"></div>
		<div class="swiper-button-next"></div> -->

		<!-- If we need scrollbar 
		<div class="swiper-scrollbar"></div> -->
	</div>
</template>
<script>
	// Import Swiper Vue.js components
	import Swiper,  { Autoplay}  from 'swiper';

	// Import Swiper styles
	import 'swiper/swiper-bundle.css';
	
	export default {
		name: 'NewsSwiper',
		data(){
			return{
				news_json: [],
				slider_bg_color:'',
			}
		},
		methods: {
			init_swiper:function () {
				Swiper.use([Autoplay]);
				window.news_swiper = new Swiper('.swiper', {
					autoplay: {
						delay: 3000,
						//waitForTransition:false,
						disableOnInteraction: false
					},
					loop: true,
					breakpoints: {  
						279: {
							slidesPerView: 2,
							spaceBetween: '2%',
							loop: true,
						},
						320: {
							slidesPerView: 2,
							spaceBetween: '2%',
							loop: true,
						},
						// when window width is >= 480px
						480: {
							slidesPerView: 3,
							spaceBetween: '2%',
							loop: true,
						},
						// when window width is >= 640px
						640: {
							slidesPerView: 3,
							spaceBetween: '2%',
							loop: true,
						},
					},

				});
			},
			getRandomInt(max) {
				return Math.floor(Math.random() * max);
			},
			getRandomColor(){
				let colors=['#1e1d4c','#4e335a', '#664662', '#743c4b', '#552138', '#3d1a38','#2a1533', '#6f596d', '#141941', '#6d3341'];
				let number=this.getRandomInt(10);
				return colors[number]+ ' !important';
			}
		},
		mounted() {
			fetch('/dataJson/news_swiper.json').then(res => res.json())
			.then(data => {
				this.news_json=data;
				setTimeout(this.init_swiper, 500);
			}).catch(err => console.log(err));
			this.slider_bg_color=this.getRandomColor();
		},
	};
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.swiper-backface-hidden .swiper-slide{
		border: 1px solid gray;
		margin-bottom: 10px;
		border-bottom: 6px solid white !important;
	}
	.swiper-wrapper{
		box-sizing: inherit;
	}
	.swiper-backface-hidden .swiper-slide div,
	.swiper-backface-hidden .swiper-slide div a{
		font-size: 1rem !important;
	}
	.swiper{
		max-width:99% !important;
	}
	@media screen and (min-width: 640px) {
		.swiper{
			max-width:99% !important;
		}
		.swiper-backface-hidden .swiper-slide{
			max-width: 32%;
		}
	}
	@media screen and (max-width: 480px){
		.swiper-backface-hidden .swiper-slide{
			max-width: 49%;
		}
	}
</style>