<script>

export default {
	name: 'mainpart',
	props: {

	},
	data() {
        return {
            retrievedObject: [],
			scholarly_data : [],
			current_route  : window.location.hash,
			api_url        : 'https://api.webscrapingapi.com/v1?api_key=iAl3YyO7XVNQmiqcyLgH6wpVg7z13DmG&url=',
			divscrolltop   : '-35px',
			years_array    : [],
			research_projects: [],
			media_and_outreach:[],
        }
    },
	methods: {
		string_to_slug:function (str, year) {
			str = str.replace(/^\s+|\s+$/g, ""); // trim
			str = str.toLowerCase();

			str = str
			.replace(/[^a-z0-9 -]/g, "") // remove invalid chars
			.replace(/\s+/g, "-") // collapse whitespace and replace by -
			.replace(/-+/g, "-") // collapse dashes
			.replace(/^-+/, "") // trim - from start of text
			.replace(/-+$/, ""); // trim - from end of text
			if(year==2016 && str=='complex-demand-scheduling-problem-with-application-in-smart-grid'){
				str=str+'_2016';
			}
			return str;
		},
		write_scholarly_json: function() {
			fetch('https://areg.wallshoot.com/create_json.php', {
				method: 'POST',
				body:JSON.stringify(this.scholarly_data),
			}).catch(err => console.log(err));
			alert('Done');
		},
		fetch_scholarly_pages: function(){
			var scholarly  = require("scholarly");
			scholarly.user("MPNNFXMAAAAJ").then((user_data) => {
				localStorage.setItem('scholarly_time', new Date());
				user_data.forEach((value, index) => {
					var _this=this;
					setTimeout(function(){
						fetch(_this.api_url+encodeURIComponent('https://scholar.google.com'+value.url)).
							then(user_data => user_data.text())
							.then(page => {
								let parser             = new DOMParser();
								let htmlDoc            = parser.parseFromString(page, 'text/html');
								var gsc_oci_title_link = htmlDoc.querySelector(".gsc_oci_title_link");
								var authors_el         = htmlDoc.querySelector(".gsc_oci_value");
								var authors            = authors_el.textContent || authors_el.innerText;
								let pdf_element        = htmlDoc.querySelector(".gsc_oci_title_ggi a");
								let pdf                = false;
								if(pdf_element){
									pdf=pdf_element.href;
								}
								if(gsc_oci_title_link!=null){
									user_data[index]["url"]=gsc_oci_title_link.href
								}else{
									var volume_el          = htmlDoc.querySelectorAll(".gsc_oci_value");
									var page_volume        = volume_el[3].textContent || volume_el[3].innerText;
									let page_number=page_volume.split(':');
									if(typeof page_number[1]!='undefined'){
										user_data[index]["url"]='https://arxiv.org/abs/'+page_number[1].trim();
									}
								}
								user_data[index]["authors"]     = authors.split(',');
								user_data[index]["pdf"]         = pdf;
								user_data[index]["page_volume"] = page_volume;
								_this.scholarly_data.push(user_data[index]);
							}).catch(err => console.log(err))
					}, index * 1000);
				});
			});
		},
		modify_authors: function(value){
			let authors=[];
			let last_author=value.authors.length-1;
			let found = false;
			if(last_author<=5){
				return false;
			}
			for(let i=0; i<=5; i++){
				authors.push(value.authors[i]);
				if(typeof value.authors[i]!='undefined' && value.authors[i].trim()=='Areg Karapetyan'){
					found=true;
				}
			}
			if(!found){
				let author_for_add='... , Areg Karapetyan';
				if(value.authors[6].trim()=='Areg Karapetyan'){
					author_for_add='Areg Karapetyan';
				}
				authors.push(author_for_add);
				if(last_author>6 && value.authors[last_author].trim()!='Areg Karapetyan'){
					authors.push('...');
				}
			}
			return authors;
		},
		getAuthors: function(authors){
			return authors.join(', ').replace('Areg Karapetyan','<span class="author_name" style="font-weight:bold">Areg Karapetyan</span>');
		},
		handleScroll () {
			this.divscrolltop = (window.scrollY-35) + "px";
		}
	},
	mounted () {
		fetch('/dataJson/research_projects.json').then(res => res.json())
			.then(data => {
				this.research_projects=data;
			}).catch(err => console.log(err));
		fetch('/dataJson/media_and_outreach.json').then(res => res.json())
			.then(data => {
				this.media_and_outreach=data;
			}).catch(err => console.log(err));
	
		var saved_time = localStorage.getItem('scholarly_time');
		if(saved_time){
			var date1   = new Date();
			var date2   = new Date(saved_time);
			var diff    = date1 - date2;
			var diffHrs = Math.floor((diff % 86400000) / 3600000); // hours
			//var diffMins = Math.round(((diff % 86400000) % 3600000) / 60000); // minutes
		}

		if((!saved_time || diffHrs>24) && this.current_route=='#update'){
			this.fetch_scholarly_pages();
			// to do 
			setTimeout(this.write_scholarly_json, 30000);
		}

		fetch('https://areg.wallshoot.com/scholarly.json').then(res => res.json())
		.then(data => {
			data=data.sort((a, b) => (a.year < b.year) ? 1 : -1);
			data.forEach((value, index) => {
				let authors=this.modify_authors(value);
				if(authors){
					data[index]["authors"]=authors;
				}
				
				if (typeof this.years_array[value.year] == 'undefined') {
					this.years_array[value.year]=index;
				}
			});
			this.retrievedObject=data;

		}).catch(err => console.log(err));
	},
	created () {
		window.addEventListener('scroll', this.handleScroll);
	},
}
</script>

