<script>
	import NewsSwiper from './NewsSwiper.vue';
	import '@/assets/css/main.css';

export default {
	components: {
		NewsSwiper
    },
	name: 'Header',
	props: {
		person_name: String
	},
	data() {
        return {
            switch_text: 'Optimize'
        }
    },
	methods: {
		toogle_particles: function(e) {
			if(e.target.checked){
				//this.particlePlay();
				this.switchText('Terminate');
				if(document.getElementById("particles-js").style.display=='none'){
					$("#particles-js").fadeIn(2000);
					$("#bg").fadeOut(2000);
					$( window ).resize();
				}
			}else{
				//this.particlePause();

					$("#particles-js").fadeOut(2000);
					$("#bg").fadeIn(2000);

				this.switchText('Optimize');
			}
		},
		particlePause:function(){
			window._tempFn = window.pJSDom[0].pJS.fn.vendors.draw;
			window.pJSDom[0].pJS.fn.vendors.draw = () => {};
		},
		particlePlay:function(){
			window.pJSDom[0].pJS.fn.vendors.draw = window._tempFn;
			window.pJSDom[0].pJS.fn.vendors.draw();
		},
		switchText:function(text){
			this.switch_text=text;
		},
	},
	mounted () {
		//this.particlePause();
		//this.particlePlay();
			
		let slider = document.querySelector('#main'),
		//sliderTrack = slider.querySelector('.slider-track'),
		sliderTrack = document.querySelector('.slider-track'),
		slides = slider.querySelectorAll('.slide'),
		//arrows = slider.querySelector('.slider-arrows'),
		//prev = arrows.children[0],
		//next = arrows.children[1],
		//slideWidth = slides[0].offsetWidth,
		slideIndex = 1,
		posInit = 0,
		posX1 = 0,
		posX2 = 0,
		posFinal = 0,
		posInitY = 0,
		posY1 = 0,
		posFinalY = 0,
		posThreshold = slides[0].offsetWidth * 0.35,
		posThresholdY = slides[0].offsetHeight * 0.25,
		trfRegExp = /([-0-9.]+(?=px))/,
		getEvent = function() {
			return (event.type.search('touch') !== -1) ? event.touches[0] : event;
		},
		
		slide = function() {
			//if (transition) {
			//	sliderTrack.style.transition = 'transform .5s';
			//}
			//sliderTrack.style.transform = `translate3d(-${slideIndex * slideWidth}px, 0px, 0px)`;

			//prev.classList.toggle('disabled', slideIndex === 0);
			//next.classList.toggle('disabled', slideIndex === --slides.length);
		},
		mswipeStart = function() {
			let evt = getEvent();

			posInit  = posX1 = evt.clientX;
			posInitY = posY1 = evt.clientY;

			sliderTrack.style.transition = '';

			document.addEventListener('touchmove', mswipeAction);
			document.addEventListener('mousemove', mswipeAction);
			document.addEventListener('touchend', mswipeEnd);
			document.addEventListener('mouseup', mswipeEnd);
		},
		mswipeAction = function() {

			let evt = getEvent(),
			style = sliderTrack.style.transform,
			transform = +style.match(trfRegExp)[0];

			posX2 = posX1 - evt.clientX;
			posX1 = evt.clientX;

			posY1 = evt.clientY;

			if((transform - posX2)>posThreshold&&5>6)
				sliderTrack.style.transform = `translate3d(${transform - posX2}px, 0px, 0px)`;
		},
		mswipeEnd  = function() {
			posFinal  = posInit - posX1;
			posFinalY = posInitY - posY1;

			document.removeEventListener('touchmove', mswipeAction);
			document.removeEventListener('mousemove', mswipeAction);
			document.removeEventListener('touchend', mswipeEnd);
			document.removeEventListener('mouseup', mswipeEnd);
			var slides =  new Object();
			slides[1]='#intro';
			slides[2]='#work';
			slides[3]='#about';
			slides[4]='#contact';
			var hashes  =  new Object();
			hashes['#intro']=1;
			hashes['#work']=2;
			hashes['#about']=3;
			hashes['#contact']=4;
			if ((Math.abs(posFinal) > posThreshold) && (Math.abs(posFinalY) < posThresholdY)) {
				let hash=document.querySelectorAll('nav a.active')[0]['hash'];
				slideIndex=hashes[hash];
				if (posInit < posX1) {
					if(slideIndex>1){
						slideIndex--;
					}else{
						slideIndex=4;
					}
					document.querySelector(slides[slideIndex]+'_icon').click();
					sliderTrack.style.transform = 'translate3d(0px, 0px, 0px)';
				} else if (posInit > posX1) {
					if(slideIndex<4){
						slideIndex++;
					}else{
						slideIndex=1;
					}
					document.querySelector(slides[slideIndex]+'_icon').click();
					sliderTrack.style.transform = 'translate3d(0px, 0px, 0px)';
				}
			}

			if (posInit !== posX1) {
				slide();
			}
		};

		sliderTrack.style.transform = 'translate3d(0px, 0px, 0px)';

		slider.addEventListener('touchstart', mswipeStart);
		slider.addEventListener('mousedown', mswipeStart);


		window.jQuery = $ = require('@/assets/js/jquery.min');
		var $ = window.jQuery;
		window.$ = $;
		window.browser = require('@/assets/js/browser.min');
		window.breakpoints = require('@/assets/js/breakpoints.min');
		require('@/assets/js/util');
		require('@/assets/js/main2');
		

	},
	computed: {
		currentRouteName() {
			return this.$route.name;
		}
	},
	setup() {
		const onSwiper = (swiper) => {
			console.log(swiper);
		};
		const onSlideChange = () => {
			console.log('slide change');
		};
		return {
			onSwiper,
			onSlideChange,
		};
    },
}

</script>

